body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body {
  margin: 0;
  padding: 0;
  /* font-family: 'Overpass', sans-serif; */
  font-family: 'Domine', serif;
  background-color: black;
  color: white;
  /* position: fixed; */
}



.homeBackground {
  background: url(/static/media/background1.ecb71e99.png) center top; 
  height: 100vh; 
  width: 100vw;
  background-size: cover;
  background-repeat: no-repeat;
  position: fixed;
}

@media (max-width: 1000px) {
  .homeBackground {
  background: url(/static/media/mobileBackground1.fe353761.png)  top left; 
  position: relative;
  height: 100vh; 
  width: 100vw;
  background-size: cover;
  background-repeat: no-repeat;
  }
}

.nameHeading {
  text-align: left; 
  color: white;
  font-size: 3rem; 
}

@media (max-width: 768px) {
  .nameHeading {
    font-size: 2rem;
  }
}

.intro {
  text-align:left;
  color:white;
  font-size:1rem;
  margin-top:20px;
  width:500px; 
  font-family: 'Noto Sans Mono', monospace;
  display: inline-block;
}

.contactLink {
  font-size:1rem;
  font-family: 'Noto Sans Mono', monospace;
  background-color: transparent;
  text-decoration: underline;
  border: none;
}

@media (max-width: 768px) {
  .intro {
    width: 300px;
  }
}

.downloadCvButton {
  padding: 10px 30px;
  color: white;
  margin-top:20px;
  font-family: 'Noto Sans Mono', monospace;
  font-weight: bolder;
  border: none;
  display: block;
}
.downloadCvButton:hover {
  border-radius: 5px;
}

.quote {
  margin-right: 200px;
  padding-left: 10px;
  color: white;
  font-size: 22pt;
  font-weight: bolder;
  float: right;
}

@media (max-width: 1200px) {
  .quote {
    display: none;
  }
}



.about-background {
  background-color: black;
  width: 100vw;
  height: 100vh;
  font-family: 'Noto Sans Mono', monospace;
}

.header {
  background-color: transparent;
  text-align: center;
}
.title {
  text-align: center;
  font-size: 20px;
  color: white;
}
h1.title {
  font-size: 30px;
  display: inline;
  padding: 0px 15px 20px 15px;
}

.container1 {
  display: flex;
  justify-content: space-between;
  margin: 110px;
}

.img-hover-zoom {
  width: 550px;
  overflow: hidden;
  border-radius: 2px;
  /* margin-top: -20px; */
}
@media (max-width: 1000px) {
  .container1 {
    display: block;
    margin: 150px;
  }
 }

@media (max-width: 768px) {
  .container1 {
    display: block;
    margin: 20px;
  }
 }

.pic {
  width: 500px;
  height:500px;
  transition: -webkit-transform .5s ease;
  transition: transform .5s ease;
  transition: transform .5s ease, -webkit-transform .5s ease;
}

@media (max-width: 768px) {
  .img-hover-zoom {
    width: auto;
    height: auto;
  }
 }

@media (max-width: 768px) {
  .pic {
    width: 96%;
    height: auto;
  }
 }

.img-hover-zoom:hover img {
  -webkit-transform: scale(1.5);
          transform: scale(1.5);
}

.wrapper {
  padding-top: 10px;
  width: 50%;
}

@media (max-width: 1000px) {
  .wrapper {
    width: auto;
    padding-top: 10px;
  }
 }

 /* experience */

 .experience {
   margin: 110px;
 }

 @media (max-width: 768px) {
  .experience {
    margin: 20px;
  }
 }

 .exp-desc {
   width: 500px;
 }

 @media (max-width: 768px) {
  .exp-desc {
    width: auto;
    margin: 10px;
  }
 }

 /* Skills */

 .skills {
  margin-left: 110px;
 }
 @media (max-width: 768px) {
  .skills {
    margin: 20px;
  }
 }

 .skills-container {
  margin-left: 0px;
 }

 .section-skill-tools {
  padding: 10px 110px 50px 0px;
 }

 .other-skills {
   padding: 10px 0px 50px 40px;
 }

.container-icons {
 text-align: center;
 margin-bottom: 3rem;
 display: flex;
 flex-direction: row;
 flex-wrap: wrap;
 margin-left: -10px;
}

.container-icons .box-icon {
  width: 15%;
  height: 142px;
  margin-bottom: 10px;
}

@media (max-width: 1000px) {
  .container-icons .box-icon {
    width: 30%;
    height: 142px;
    margin-bottom: 10px;
  }
 } 

 @media (max-width: 768px) {
  .container-icons .box-icon {
    width: 50%;
    height: 142px;
    margin-bottom: 10px;
  }
 } 



.main-container {
  font-family: 'Noto Sans Mono', monospace;
  margin: 30px 140px;
}

@media (max-width: 1200px) {
  .main-container {
    margin: 30px 50px;
  }
 }

.project-header {
  background-color: transparent;
  text-align: center;
}

@media (max-width: 1000px) {
  .project-header h2 {
    font-size: 18px;
  }

  .project-header h5 {
    font-size: 12px;
  }
 }

.container-work {
  display: flex;
  justify-content: space-between;
}

.container-text {
  flex:3 1;
  margin: 30px;
}

.container-img {
  flex: 1 1;
  margin: 30px;
}

.container-img img {
  width: 500px;
}

.view-button {
  color: white;
  padding: 10px 20px;
  text-decoration: none;
  border: none;
  font-size: 14px;
  font-weight: bold;
}

.view-button:hover {
  border-radius: 5px;
}

@media (max-width: 1300px) {
  .container-img img {
    width: 300px;
  }
  .container-text {
    margin: 0px;
  }

  .view-button {
    padding: 10px;
  }
}

@media (max-width: 768px) {
  .container-work {
    display: block;
  }

  .container-img {
    margin: 0px auto;
  }

  .container-img img {
    width: 280px;
  }

  .container-text {
    margin: 0px;
  }

  .view-button {
    padding: 10px;
    font-size: 12px;
  }
}



.container-work:nth-child(odd) .container-text {
  order: -1;
}


